import { useState, useEffect, useRef } from "react";
import Dots from "../element/Dots.js";
import ArtifactForm from "../element/ArtifactForm";
import CharacterCal from "../element/CharacterCal";
import SkillShow from "../element/SkillShow";
import "./Main.css";
import maintheme from "../music/PhantasmSuite.mp3";
import img1 from "../img/character/raiden.webp";
import moff from "../img/icon/bgm_off.png";
import mon from "../img/icon/bgm_on.png"
import VideoBg from "reactjs-videobg";
import mp4 from "../video/GBG.mp4";

const DIVIDER_HEIGHT = 5;

function Main() {
  const outerDivRef = useRef();
  const [scrollIndex, setScrollIndex] = useState(1);
  useEffect(() => {
    const wheelHandler = (e) => {
      e.preventDefault();
      const { deltaY } = e;
      const { scrollTop } = outerDivRef.current; //    
      const pageHeight = window.innerHeight; //  , 100vh .

      if (deltaY > 0) {
        //   
        if (scrollTop >= 0 && scrollTop < pageHeight) {
          // 1
          console.log(" 1, down");
          outerDivRef.current.scrollTo({
            top: pageHeight + DIVIDER_HEIGHT,
            left: 0,
            behavior: "smooth",
          });
          setScrollIndex(2);
        } else if (scrollTop >= pageHeight && scrollTop < pageHeight * 2) {
          // 2
          console.log(" 2, down");
          outerDivRef.current.scrollTo({
            top: pageHeight * 2 + DIVIDER_HEIGHT * 2,
            left: 0,
            behavior: "smooth",
          });
          setScrollIndex(3);
        } else if (scrollTop >= pageHeight && scrollTop < pageHeight * 3) {
          //  3
          console.log(" 3, down");
          outerDivRef.current.scrollTo({
            top: pageHeight * 3 + DIVIDER_HEIGHT * 3,
            left: 0,
            behavior: "smooth",
          });
          setScrollIndex(4);
        } else {
          //  4
          console.log(" 4, down");
          outerDivRef.current.scrollTo({
            top: pageHeight * 4 + DIVIDER_HEIGHT * 4,
            left: 0,
            behavior: "smooth",
          });
          setScrollIndex(4);
        }
      } else {
        //   
        if (scrollTop >= 0 && scrollTop < pageHeight) {
          // 1
          console.log(" 1, up");
          outerDivRef.current.scrollTo({
            top: 0,
            left: 0,
            behavior: "smooth",
          });
          setScrollIndex(1);
        } else if (scrollTop >= pageHeight && scrollTop < pageHeight * 2) {
          // 2
          console.log(" 2, up");
          outerDivRef.current.scrollTo({
            top: 0,
            left: 0,
            behavior: "smooth",
          });
          setScrollIndex(1);
        } else if (scrollTop >= pageHeight * 2 && scrollTop < pageHeight * 3) {
          //  3
          console.log(" 3, up");
          outerDivRef.current.scrollTo({
            top: pageHeight + DIVIDER_HEIGHT,
            left: 0,
            behavior: "smooth",
          });
          setScrollIndex(2);
        } else {
          //  4
          console.log(" 4, up");
          outerDivRef.current.scrollTo({
            top: pageHeight * 2 + DIVIDER_HEIGHT * 2,
            left: 0,
            behavior: "smooth",
          });
          setScrollIndex(3);
        }
      }
    };
    const outerDivRefCurrent = outerDivRef.current;
    outerDivRefCurrent.addEventListener("wheel", wheelHandler);
    return () => {
      outerDivRefCurrent.removeEventListener("wheel", wheelHandler);
    };
  }, []);

  const options = {
    root: null, // viewport
    rootMargin: "0px",
    threshold: .5,  // 50% viewport   callback 
  }

  const observer = new IntersectionObserver(entries => {
    entries.forEach(entry => {
      if (entry.isIntersecting) {
        entry.target.classList.add("active");
      } else {
        entry.target.classList.remove("active");
      }
    });
  }, options);

  const titleList = document.querySelectorAll('img');

  titleList.forEach(el => observer.observe(el));

  const divList = document.querySelectorAll('div, .AF');

  divList.forEach(el => observer.observe(el));

  const [musics, setMusics] = useState(true);
  let mbtn = mon;
  function muteOnClick() {
      const music = document.querySelector(".music");
      const mute = document.querySelector(".mute");
      if (musics === true) {
        mute.src = moff;
        setMusics(false);
        music.volume = 0;
        console.log("");
      } else {
        mute.src = mon;
        setMusics(true);
        music.volume = 1;;
        console.log("");
      }
  };
  /*useEffect(()=>{
    const chsrc = document.querySelector(".imgraiden");
    const backgroundArr = [img1, logo, img1, logo];
    const randomIndex = Math.floor(Math.random() * backgroundArr.length);
    const backgroundImg = backgroundArr[randomIndex];
    console.log(backgroundImg);
    chsrc.src = (backgroundImg);
  }, []);*/
  
  return (
    <div ref={outerDivRef} className="outer">
      <audio autoPlay="autoplay" loop className="music">
        <source src={maintheme} type="audio/mp3" />
      </audio>
      <Dots scrollIndex={scrollIndex} />
      <img src={mbtn} alt="" className="mute" onClick={muteOnClick}></img>
      <div className="inner bg1">
          <VideoBg>
            <VideoBg.Source src={mp4} type="video/mp4" className="gbgbg" />
          </VideoBg>
      </div>
      <div className="divider"></div>
      <div className="inner bg2">
        <div className="AF">
          <ArtifactForm />
        </div>
        <img src={img1} className="leftright imgraiden" alt="img"></img>
      </div>
      <div className="divider"></div>
      <div className="inner bg3">
          <CharacterCal />
      </div>
      <div className="divider"></div>
      <div className="inner bg4">
          <SkillShow />
      </div>
    </div>
  );
}

export default Main;
